import { format, formatDistance, formatRelative } from 'date-fns'
import { zhCN } from 'date-fns/locale'

export const formatErrorMsg = err => {
  let msg = '出错信息: '
  if (err.response && err.response.data && err.response.data.message) {
    msg += `${err.response.data.message}. `
    err.response.data.errors && (msg += err.response.data.errors.map(e => e.message).join(', '))
  } else {
    msg += err.message
  }
  if (msg.includes('Network') || msg.includes('timeout')) {
    return '加载失败，阅读微博需要翻墙哦~'
  }

  if (msg.includes('rate limit exceeded')) {
    return '微博接口已被请求爆了，请过一会再来吧~'
  }

  return msg
}

export const filterDate = _date => {
  const date = new Date(_date)
  const now = new Date()
  const formattedDate = formatDistance(date, now, { addSuffix: true, locale: zhCN })

  if (formattedDate.includes('秒') || formattedDate.includes('分钟')) {
    return formattedDate
  }

  if (formattedDate.includes('小时') || formattedDate.includes('昨天')) {
    return formatRelative(date, now, { locale: zhCN })
  }

  if (formattedDate.includes('天')) {
    return formattedDate
  }

  return format(date, 'yyyy-MM-dd', { locale: zhCN })
}

export const filterLabels = labels => {
  if (!labels.length) {
    return { name: '🎨其他', color: '999' }
  }
  return {
    name: labels[0].name, color: labels[0].color
  }
}

export const filterIssues = issues => {
  return issues.map(({ createdAt, bodyHTML, number, id, comments: { totalCount: comments }, labels }) => {
    const label = filterLabels(labels.nodes)
    return { createdAt, bodyHTML, number, gId: id, comments, label }
  })
}

export const filterComment = comment => {
  return {
    id: comment.databaseId,
    gId: comment.id,
    user: {
      avatar: comment.author.avatarUrl,
      login: comment.author.login
    },
    createdAt: comment.createdAt,
    bodyHtml: comment.bodyHTML,
    body: comment.body,
    reactions: comment.reactions
  }
}
export const filterComments = comments => {
  return comments.map(comment => filterComment(comment))
}

export const getColorByBgColor = hexcolor => {
  let r = parseInt(hexcolor.substr(0, 2), 16)
  let g = parseInt(hexcolor.substr(2, 2), 16)
  let b = parseInt(hexcolor.substr(4, 2), 16)
  let yiq = ((r * 299) + (g * 587) + (b * 114)) / 1000
  return (yiq >= 128) ? 'black' : 'white'
}

export const queryParse = (search = window.location.search) => {
  if (!search) return {}
  const queryString = search[0] === '?' ? search.substring(1) : search
  const query = {}
  queryString
    .split('&')
    .forEach(queryStr => {
      const [key, value] = queryStr.split('=')
      /* istanbul ignore else */
      if (key) query[decodeURIComponent(key)] = decodeURIComponent(value)
    })

  return query
}

export const queryStringify = query => {
  const queryString = Object.keys(query)
    .map(key => `${key}=${encodeURIComponent(query[key] || '')}`)
    .join('&')
  return queryString
}

export const getIssueUrl = number => {
  const { repo, owner } = window.WEIBO
  return `https://github.com/${owner}/${repo}/issues/${number}`
}

export const getCommentUrl = number => {
  const { repo, owner } = window.WEIBO
  return `https://api.github.com/repos/${owner}/${repo}/issues/${number}/comments`
}